.Wrapper img {
    width: 100%;
    border-start-start-radius: 20px;
    border-start-end-radius: 20px;
    height: 200px;
  }
  .Wrapper {
    padding: 2%;
  }
  .card {
    border: 1px solid #0066cc;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .texts {
    padding: 5%;
    margin-top: 1%;
  }
  .texts h3 {
    margin: 3% 0;
  }
  .texts p {
    margin-bottom: 2%;
  }
  
  .buttons {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 4%;
  }
  .buttons button {
    padding: 10px 20px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .delete {
    background-color: red;
    border: 1px solid red;
    color: white;
  }
  .put {
    background-color: #0066cc;
    color: white;
    border: 1px solid #0066cc;
  }
  